import React,{useState} from 'react';
import Modal from 'react-bootstrap/Modal'
import {
    EmailIcon, EmailShareButton, FacebookShareButton,TwitterShareButton,FacebookIcon,TwitterIcon,
} from 'react-share'


const ShareModal =({newsWeb, url, title}) => {
    const [show, setShow] = useState(false);
   
    const handleClick=(event)=> {

      event.stopPropagation();
      
      setShow(true)
      
    }
    

    return(
        <>
        <button style = {{"background":"none", "border" :"none"}} onClick = {handleClick}>
            <i className="fas fa-share-alt" style = {{"fontSize": "18px"}}></i>
        </button>
        <Modal
        onClick ={(event)=> {
          event.stopPropagation()

        }}
        show={show}
        onHide={() => setShow(false)}
        aria-labelledby="example-modal-sizes-title-sm"
      >
        <Modal.Header closeButton>
          <Modal.Title id="example-modal-sizes-title-sm" >
          <h1 className = "modalTitle"  style = {{fontSize:"20px", fontWeight:"bolder", "textTransform":"uppercase"}}>{newsWeb}</h1>
            <h1 className = "modalTitle" >{title}</h1>
          </Modal.Title>
        </Modal.Header>
        <Modal.Body >
            <p style={{"font-size":"20px", "textAlign":"center", "marginBottom":"5px"}}>Share via</p>
            <div className = "row">
                <FacebookShareButton className = "col-md-4 col-4" url={url}
                quote={title}><FacebookIcon size={50} round={true}/></FacebookShareButton>
                <TwitterShareButton className = "col-md-4 col-4" url={url}
                title={title}><TwitterIcon size={50} round={true}/></TwitterShareButton>
                <EmailShareButton url = {url} subject = {title}  className = "col-md-4 col-4" ><EmailIcon size={50} round={true}/></EmailShareButton>

            </div>
            

        </Modal.Body>
      </Modal>
      </>
    )
    
}

export default ShareModal;