import React from 'react';

const colorList = {
    'world': {'background': 'rgb(119, 70, 248)', 'wordcolor': 'white'},
    'politics': {'background': 'rgb(41, 137, 125)', 'wordcolor': 'white'},
    'business': {'background': 'rgb(51, 140, 228)', 'wordcolor': 'white'},
    'technology': {'background': 'rgb(196, 214, 71)', 'wordcolor': 'black'},
    'sports': {'background': 'rgb(249, 185, 75)', 'wordcolor': 'black'},
    'health': {'background': 'rgb(98, 106, 112)', 'wordcolor': 'white'},
    'guardian': {'background': 'rgb(19, 36, 64)', 'wordcolor' :'white'},
    'nytimes':{'background': 'rgb(216, 216, 216)', 'wordcolor': 'black'}
}

const Tag = ({section, name, size}) => {
   
   var sectionName = String(section).toLocaleLowerCase();
    if (sectionName === 'sport') {
        sectionName = 'sports'
    }
   
    let tagWord, tagColor, wordColor;

    Object.entries(colorList).map(([key, value]) => {
       
        if (key === sectionName) {
            tagWord = key;
            Object.entries(value).map(([key, value]) => {
                if (key === 'background') {
                    tagColor = value;
                }else {
                    wordColor = value;
                }
               
               
                return key;
            
            
        })
    }
        return key;
        
        
    })
    if (tagWord == null){
        tagWord = section;
        tagColor = 'rgb(98, 106, 112)';
        wordColor = 'white';
    }
    const style ={
        
        "fontWeight": "bolder",
        "fontSize": size,
        "textTransform":"uppercase",
        background: tagColor,
        color:wordColor,
        padding:"3px",
        "borderRadius":"5px",
        height:"auto",
        width:"fit-content",
        float:"right"
        

    
    }
    return (
        <>
        <div className = {name}>
            <p style = {style}> {tagWord}</p>

        </div>
            
      
        
        </>
    )


}
export default Tag;