import React,{Component} from 'react';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';

import {BrowserRouter as Router} from 'react-router-dom'
import Switch from 'react-switch'
import { Redirect} from 'react-router-dom';
import AsyncSelect from 'react-select/async';
import {FaRegBookmark, FaBookmark,FaBars} from 'react-icons/fa'


const favStyle = {
  color : "white",
    "fontSize" : "20px",
  "marginLeft": "20px",
  "marginRight":"30px",

  display:'block'
};
const favStyle2= {
color : "white",
"marginLeft": "20px",
  "fontSize" : "20px",
  "marginRight":"30px",

  display:"none"
};

class Navigation extends Component {

  
  constructor() {
    super();
    if (localStorage.getItem('newsWeb') === 'true' || localStorage.getItem('newsWeb') == null) {
      this.state = { checked: true, inputValue:'', results: [], selected: '', selectedResult: '', redirect: false, directToLove: false};
    }else if (localStorage.getItem('newsWeb') === 'false'){
      this.state = {checked: false};
    }
    this.handleChange = this.handleChange.bind(this);
  }

  renderRedirect= selectedResult => {
    if (this.state.redirect) {
        console.log(selectedResult)
        let path = '/search?q='+ selectedResult;
        if (this.props.page ==='result') {
          window.location.reload(false)
        }  
        return <Redirect to={path}/>
    }
  }
  

  handleSearchChange = async (event, inputValue) => {
    const value =  this.state.inputValue;
    console.log(value)
    const url = 'https://api.cognitive.microsoft.com/bing/v7.0/suggestions?mkt=fr-FR&q=' + value;
    try {
      const response = await fetch(url,
        {
          headers: {
            "Ocp-Apim-Subscription-Key": "efc973d090324002b8edc9c95fef39f1"
          }
        }
      );
      const data = await response.json();
      const resultsRaw = data.suggestionGroups[0].searchSuggestions;
      const results = resultsRaw.map(result => ({ value: result.displayText, label: result.displayText, url: result.url, color: '#00B8D9', }));
      console.log(results)
      this.setState({ results });
    } catch (error) {
      console.error(`Error fetching search`);
    }
    return this.state.results
  
  };

  handleResultSelect = (e, { result }) =>
    this.setState({ selectedResult: result });


  handleInputChange = (newValue) => {
    const inputValue = newValue.replace(/\W/g, '');
    this.setState({ inputValue });
    return inputValue;
  };
  
  handleChange(checked) {
    this.setState({ checked});
    if (checked) {
      localStorage.setItem('newsWeb','true')
    }else {
      localStorage.setItem('newsWeb','false')
    }
    window.location.reload(false)
  }
  Change = selected=> {
    this.setState(
      {
        selected,
        selectedResult: selected.label,
        redirect:true
      }
    )
    
  }
  toFavorite= directToLove => {
    this.setState({
      directToLove
    })
  
  }
  renderFavoritePage=()=>{
    if (this.state.directToLove) {
      let path = '/favorite'
      return <Redirect to = {path}/>
    }
  }
  

  componentDidMount () {
    const script = document.createElement("script");
    script.src = "https://kit.fontawesome.com/6422077f34.js";
    script.crossOrigin="anonymous";
    script.async = true;
    document.head.appendChild(script);
  }
  render() {
    
    const activeKey = this.props.activeKey;
    const display = this.props.display;
   
    return (
      <>
      <div>
        {this.renderFavoritePage()}
        {this.renderRedirect(this.state.selectedResult)}
        
        <Router>
        <Navbar collapseOnSelect expand="md" bg="dark" variant="dark">

              <AsyncSelect
                textAlign = 'left'
                noOptionsMessage = {()=> "No Match"}
                placeholder = {"Enter keyword.."}
                onChange ={this.Change}
                className="search col-md-3 col-10"
                inputValue = {this.props.searchWord}
                cacheOption
                loadOptions={this.handleSearchChange}
                onInputChange={this.handleInputChange}
                value ={"this.state.selected"}
                />   
          <Navbar.Toggle aria-controls="responsive-navbar-nav" className = "col-2" style ={{padding:"5px"}}/>
          <Navbar.Collapse id="responsive-navbar-nav" style ={{textAlign:"left"}} >
          <Nav className="mr-auto" defaultActiveKey="home" activeKey = {activeKey} style ={{textAlign:"left","paddingLeft":"20px"}}>
              <Nav.Link eventKey="home" href="/home" >Home</Nav.Link>
                <Nav.Link eventKey="world" href = "/world">World</Nav.Link>
                <Nav.Link eventKey="politics" href = "/politics">Politics</Nav.Link>
                <Nav.Link eventKey="business" href = "/business">Business</Nav.Link>
                <Nav.Link eventKey="technology" href = "/technology">Technology</Nav.Link>
                <Nav.Link eventKey="sport" href = "/sport" >Sports</Nav.Link> 
            </Nav>
           
                <FaRegBookmark id = "fareg"size={20} style={favStyle} onClick = {this.toFavorite} />
                 <FaBookmark id = "fa" size={20} style={favStyle2} onClick = {this.toFavorite}/>

            
            <div  style = {{display:display}} className = "newsLabel" >NYTime</div>
            <div style = {{display:display}} className ="toggleSwitch">
              <Switch
                onChange={this.handleChange}
                checked ={this.state.checked}
                onColor="#2693e6"
                uncheckedIcon={false}
                checkedIcon={false}
                className = "switch"
                height={30}
                /> 
            </div>
            <div style = {{display:display}}className = "newsLabel2">Guardian</div>
          </Navbar.Collapse>
        </Navbar>
       
        </Router> 
        
        </div>
      </>
    );
  }
}


export default Navigation;

