import React, { Component } from 'react';
import Navigation from '../components/Navigation';
import News from '../components/News'



class HomePage extends Component{
    
    constructor(){
        super();
        this.update = this.update.bind(this);
     
    }
    
    update(){
        this.forceUpdate();
    }
    
    render(){
        if (localStorage.getItem("newsWeb" === null)) {
            localStorage.setItem("newsWeb", 'true')

        }
        console.log(localStorage.getItem("newsWeb"))
         let section = '';
         console.log(this.props.match.params.id)
        if (this.props.match.params.id == undefined) {
        section = 'home'
        
        }else {
            section = this.props.match.params.id;
        }
        console.log(localStorage.getItem("newsWeb"))
        if (localStorage.getItem("newsWeb") === null) {
            localStorage.setItem("newsWeb", "true")
        }
        console.log(section)
        return (
            <> 
           
            <Navigation activeKey = {section} display = {'block'}/>
            
            <News section = {section} newsWeb = {localStorage.getItem("newsWeb")} page= {'home'}/> 
            </>
        )

    }
    

}
    


export default HomePage;