import React, { Component } from 'react';
import Navigation from '../components/Navigation';
import queryString from 'query-string'
import News from '../components/News'

class ResultPage extends Component{
    componentDidMount() {
        
    }
    render(){
        let q = queryString.parse(this.props.location.search).q
        console.log(q)
        

        return(
            <>
            
            <Navigation activeKey = {null} display = {'none'} page = {'result'} searchWord = {q}/>
            <h1 style = {{textAlign:"left", marginLeft: "15px", marginTop:"20px", marginBottom:"-20px", padding: 0 , fontSize: "25px"}}>Results</h1>
            <News page = {'result'} newsWeb = {localStorage.getItem('newsWeb')} q = {q} />
            </>
        )

    }
   

}

export default ResultPage;