import React, { Component } from 'react';
import scrollToComponent from 'react-scroll-to-component'
import {
    EmailIcon, EmailShareButton, FacebookShareButton,TwitterShareButton,FacebookIcon,TwitterIcon,
} from 'react-share'
import {FaRegBookmark,FaChevronDown,FaChevronUp, FaBookmark} from 'react-icons/fa'
import ReactToolTip from 'react-tooltip'
import Comment from '../components/Comment'
import { toast,ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

class Article extends Component {

    componentDidMount () {
        let description = this.props.description;
        let res= description.replace(/([.])\s*(?=[A-Z])/g, "$1|").split("|")
        console.log(res[0])
        if (res.length > 7) {
            for (var i = 0; i < 6; i++) {
                document.getElementById("show-description").append(res[i]);
                document.getElementById("downArrow").style.display = "block";
                document.getElementById("upArrow").style.display = "none";
            }
            for (var i = 6; i < res.length;i++) {
                document.getElementById("hide-description").append(res[i]);
            }
        }else {
            for (var i = 0; i < res.length;i++) {
                document.getElementById("show-description").append(res[i]);
            }
            document.getElementById("downArrow").style.display = "none";
            document.getElementById("upArrow").style.display = "none";
        }
    }
   
    addFavorite=() =>{
        

        var marked = document.getElementById('fa2').style.display;
        var favoritesList = JSON.parse(localStorage.getItem("favorites"))
        const {id, title, img, section, date,} = this.props;
        if (marked === 'block') {
            var toastContent = "Removing "+title;
            toast(toastContent, { autoClose: 4000 })
            delete favoritesList[id];
            localStorage.setItem("favorites", JSON.stringify(favoritesList))
            
            document.getElementById('fareg2').style.display = 'block'
            document.getElementById('fa2').style.display = 'none'
        }else {
            var toastContent = "Saving "+title;
            toast(toastContent, { autoClose: 4000 })
            document.getElementById('fareg2').style.display = 'none'
            document.getElementById('fa2').style.display = 'block'

            if (favoritesList === null) {
                favoritesList = {}
                localStorage.setItem("favorites",JSON.stringify(favoritesList))
                console.log(JSON.parse(localStorage.getItem("favorites")))
            }
                
                console.log(id)
                var newsWeb = localStorage.getItem("newsWeb") === 'true' ? 'guardian' : 'nytimes'
                console.log(newsWeb)
    
                favoritesList[id] = {titleLove:title, imgLove:img, sectionLove: section, dateLove: date, newsWebLove: newsWeb}
                localStorage.setItem("favorites", JSON.stringify(favoritesList))
                console.log(JSON.parse(localStorage.getItem("favorites")))
        }        
    }

    render() {
        const {url, id, title, img, section, date, description} = this.props;
        console.log(id)
        let dateObj = new Date(date);
        let convertDate = dateObj.toISOString().substring(0,10); 
        var favoritesList = JSON.parse(localStorage.getItem("favorites"))
        var faRegmarked = 'block';
        var famarked = 'none';
        if (favoritesList !==null) {
            if (favoritesList[id] == undefined) {
                faRegmarked = 'block'
                famarked = 'none'
            }else {
                famarked = 'block'
                faRegmarked = 'none'
            }
        }
        return(
            <>
                <div className ="detail">
                    
                    <ToastContainer style = {{"textAlign": "left"}} position = "top-center" hideProgressBar/>
                    <h1 className = "col-md-12 col-12 detail-title">{title}</h1>
                    <div className = "row" style = {{padding: "15px"}}>
                    <p className = "col-md-9 detail-date col-5">{convertDate}</p>
                    <div className = "col-md-2 col-5">
                        <ReactToolTip />
                        <FacebookShareButton url={url} quote={title}><FacebookIcon data-tip="Facebook" size={30} round={true}/></FacebookShareButton>
                        <TwitterShareButton url={url}  title={title}><TwitterIcon data-tip="Twitter" size={30} round={true}/></TwitterShareButton>
                        <EmailShareButton url = {url} subject = {title} ><EmailIcon data-tip="Email" size={30} round={true}/></EmailShareButton>
                    </div>
                    <div className = "col-md-1 col-2" onClick = {this.addFavorite}>
                        <ReactToolTip />
                        <FaRegBookmark id = "fareg2" data-tip="Bookmark" size={28} color={'red'} style={{display:faRegmarked}}/>
                        <FaBookmark id = "fa2" data-tip="Bookmark" size={28} color={'red'} style ={{display:famarked}}/>
                    </div>
                    </div>
                    
                    <div className = "col-md-12 detail-img">
                        <img src = {img} alt ="Ssss"></img>
                    </div>
                    <p id = "show-description" className = "col-md-12 detail-description p2" ref = {(p) => {this.p2 = p;}}></p>
                    <p id = "hide-description" style = {{'display':'none'}} className = "col-md-12 detail-description "  ref = {(p) => {this.p1 = p;}} ></p>
                    <div className = "arrow col-md-12 col-12">
                        <i id = "downArrow" 
                            onClick = {()=> {
                            document.getElementById("hide-description").style.display = 'block';
                            scrollToComponent(this.p1,{ offset: 0, align: 'top', duration: 500});
                            document.getElementById('downArrow').style.display = "none";
                            document.getElementById('upArrow').style.display = "block";
                            }}>
                            <FaChevronDown /> 
                        </i>   
                        <i id = "upArrow" 
                            onClick = {()=> {
                            document.getElementById("hide-description").style.display = 'none';
                            scrollToComponent(this.p2,{ offset: 0, align: 'top', duration: 500});
                            document.getElementById('downArrow').style.display = "block";
                            document.getElementById('upArrow').style.display = "none";
                            }}>
                                <FaChevronUp /> 
                        </i> 

                   
                    </div>
                    <Comment className = "col-md-12 col-12 " id = {id}/>
                    
                </div>
                
                
            </>
        )
    }
}
export default Article;