import React, {Component} from 'react';
import NewsCard from './NewsCard';
import Article from './Article';
import ResultCard from './ResultCard'
import BounceLoader from "react-spinners/BounceLoader";
import {css} from "@emotion/core"


const override = css`
  display: block;
  margin:auto;
  border-color: red;
`;
    
class News extends Component {
    constructor(props) {
        super(props);
        this.state = {
            guardian:'',
            newsList:[],
            title:"",
            loading:true
        };
    }
    async componentDidMount(){
        async function getNewsAsync(url) {
            let response = await fetch(url)
            let data = await response.json()
            return data;
        }
        let url
        console.log(this.props.newsWeb)
        if (this.props.newsWeb === 'true') {
            this.setState({guardian : true})
            console.log(this.state.guardian)
            if (this.props.page !== 'article') {
                if (this.props.page === 'home') {
                    url ='/api/guardian/'
                    url+=this.props.section;
                    console.log(url)
                }else if (this.props.page === 'result') {
                    url ='/api/guardian/search?q='
                    url+=this.props.q
                }
                getNewsAsync(url)
                .then(data => {
                this.setState({
                newsList : data.response.results,
                loading: false                   
                });
                }) 
            }else {
                url = 'api/guardian/article?id='
                url += this.props.id;
                getNewsAsync(url)
                .then(data => {
                var res = [];
                res.push(data.response.content)
                this.setState({
                newsList : res  ,
                loading: false                 
                });
            }) 
            }  
        }else {
            this.setState({guardian:false})
            console.log(this.state.guardian)
            if (this.props.page !== 'home') {
                if (this.props.page === 'article'){
                    url = 'api/nytime/article?id='
                    url += this.props.id;
                }else if (this.props.page === 'result') {
                    url ='/api/nytime/search?q='
                    url += this.props.q
                }
                getNewsAsync(url)
                .then(data => {
                this.setState({
                    newsList : data.response.docs   ,
                    loading: false               
                    });
                })
            }else {
                url ='/api/nytime/'
                if (this.props.section === 'sport'){
                    url+='sports'
                }else {
                    url+=this.props.section;
                }
                console.log(url)
                
                getNewsAsync(url)
                .then(data => {
                this.setState({
                    newsList : data.results  , 
                    loading:false           
                    });
                })
            }
        }

    }
    
    
    
    render() {
        const loading = this.state.loading;
        let count = 0;
        console.log(this.state.newsList)
         
        
        if (loading) {
            return(
                <>
                <main>
                    <div className="sweet-loading">
                    <BounceLoader
                        css={override}
                        size={50}
                        color={"rgb(58,88,150)"}
                        loading={this.state.loading}
                    />
                    Loading...
                    </div>

                </main>
                </>
            )
        }else {
            if (this.state.newsList.length === 0 && this.props.page === 'result') {
                return (
                    <>
                    No results
                    </>
                )
            }
            console.log(this.state.guardian)
            if(!this.state.guardian) {
                return <main>
                    <div className = "row">
                    
                    {
                        this.state.newsList.map((news,index) => {
                            let title = news.title

                            let section;
                            if (this.props.section === 'politics'){
                                section = news.subsection
                            }else {
                                section = news.section
                            }
                            console.log(section)
                            
                            let date = news.published_date
                            let description = news.abstract
                            let img =''
                            let id = news.url;
                            if (news.multimedia !== null) {
                                news.multimedia.map((image, index) => {
                                    if (image.width >= 2000) {
                                        img = image.url
                                        return index
                                    }
                                    
                                })
                            }
                            
                            if (title !== null && section !== null && date !== undefined && description !== null &&
                                id !== null && this.props.page === 'home' && count<10) {
                                if(img === '') {
                                    img = 'https://upload.wikimedia.org/wikipedia/commons/0/0e/Nytimes_hq.jpg'
                                }
                                count++;
                                return (
                                    <div className = "col-12 col-md-12 col-xl-12">
                                    <NewsCard url = {id} id = {id} title = {title} img ={img} section = {section} date = {date} description = {description} key= {index} />
                                    </div>
                                )
    
                            }else if (description !== null && this.props.page !== 'home'){
                                id  = news.web_url
                                title = news.headline.main
                                date = news.pub_date
                                if(img === '') {
                                    img = 'https://upload.wikimedia.org/wikipedia/commons/0/0e/Nytimes_hq.jpg'
                                }else {
                                    img = 'https://nyt.com/' + img
                                }
                                section = news.news_desk
                                console.log(section)
                                if (section !== '' && title !== null && date !== null &&  count<10){
                                    if (this.props.page === 'article'){
                                        count++;
                                    return(
                                        <div className = "col-12 col-md-12 col-xl-12">
                                        <Article  url = {id} id = {id} title = {title} img={img} date = {date} section = {section} description = {description} key= {index} />
                                        </div>
                                    )}else if (this.props.page === 'result'){
                                        count++;
                                        return(
                                            <ResultCard  url = {id}  id = {id} title = {title} img ={img} section = {section} date = {date} description = {description} key= {index}/>
                                        )

                                    }
                                }   
                            } 
                        })
    
                    }
                </div>
                </main>
            }else {
                console.log(this.props.section)

                return <main>
                    <div className = "row">
                  
                        {
                        this.state.newsList.map((news,index) => {
                            let title =news.webTitle
                            let section = news.sectionId
                            let date = news.webPublicationDate
                            console.log(date)
                            let description
                            let id = news.id
                            let url = news.webUrl
                            if (news.blocks != undefined) {
                                if (news.blocks.body !== undefined) {
                                    news.blocks.body.map((content, index) => {
                                        if (index === 0) {
                                            description = content.bodyTextSummary;
                                            return content.bodyTextSummary
                                        }
                                        return null
                                    })

                            }
                            }
                            
                            let img = ''
                            
                            if (news.blocks !== undefined){
                                if (news.blocks.main !== undefined) {
                                        if (news.blocks.main.elements !== undefined) {
                                            news.blocks.main.elements.map((element, index) => {
                                                if (index === 0) {
                                                    return element.assets.map((asset,index) => {
                                                        if (index === element.assets.length -1 ) {
                                                            img = asset.file;
                                                            return asset.file;
                                                        }
                                                        return asset
                                                    })
                                                }
                                                return element
                    
                                            })  

                                    }

                                    

                                }
                            }
                            
                            if (img === '') {
                                img = 'https://assets.guim.co.uk/images/eada8aa27c12fe2d5afa3a89d3fbae0d/fallback-logo.png'
                            }
                            if (title !== null && section !== null && date !== null && description !== null &&
                                id !== null && count <10) {
                                    if (this.props.page === 'home') {
                                        return (
                                            <div className= "col-12 col-md-12 col-xl-12">
                                            
                                            
                                            <NewsCard url = {url} id = {id} title = {title} img ={img} section = {section} date = {date} description = {description} key= {index} />
                                            </div>
                                           
                                        )
                                    }else if (this.props.page === 'article'){
                                        return(
                                            <div className = "col-12 col-md-12 col-xl-12">
                                        
                                            <Article url = {url} id = {id} title = {title} img={img} date = {date} section = {section} description = {description} key= {index} />
                                            </div>
                                        )
                                    }else if (this.props.page === 'result') {
                                        return(
                                            <ResultCard url = {url} id = {id} title = {title} img ={img} section = {section} date = {date} description = {description} key= {index} />
            
                                        )
                                    }

                                }                   
                        })
                    }
                    </div>
                    
                    </main>
                    
                }
        }
           
    }
}


export default News