import React, {Component} from 'react';
import Tag from './Tag'
import ShareModal from './ShareModal';
import { Redirect} from 'react-router-dom';



class NewsCard extends Component
{   
    state = {
        redirect: false,
        idPath: ''
    }
    setRedirect =() => {
        this.setState({
            redirect : true,
            
        })
        
    }
    renderRedirect=(id) => {
        

        if (this.state.redirect) {
            console.log(id)
            
            let path = '/article?id='+ id;
            
           //let path='/favorite'
            return <Redirect to={path}/>
        }
    }
    //console.log(redirect)
   
    render() 
    {   
        const {url, id, title, img, section, date, description} = this.props;
        
        console.log(id)
        let classname = "col-md-6"
        let dateObj = new Date(date);
        let imgurl = 'url(' + img + ')'
        let convertDate = dateObj.toISOString().substring(0,10); 
        
        return(
            <>
            
                {this.renderRedirect(id)}
            
            <div className ="newsCard"  onClick = {this.setRedirect}>
                
            <div className = "row">

           
            <div className="imgContainer col-xl-3 col-md-3 col-lg-3 col-12 " >
                <div className = "border">
                    <div className ="newsCard-img" src={img} alt="picture" style = {{backgroundImage: imgurl}}></div>
                </div>
                
            </div> 
            
            <div className="contentContainer  col-xl-9 col-md-9 col-lg-9 col-12">
                <h1 className = "newsCard-title">{title}
                <ShareModal url = {url} title = {title}></ShareModal>
                </h1>
                
                <p className = "newsCard-description">{description}</p>
                <div className="row">
                    <p className = "col-xl-6 col-md-6 newsCard-date col-6">{convertDate}</p>
                    <div className = "col-xl-6 col-md-6 col-6" style = {{textAlign:"right"}}>
                    <Tag section = {section} ></Tag> 

                    </div>
                     
                </div>
            </div>
                
                
            </div>
            </div>
           
            </>
           
        )

    }
    
}  

export default NewsCard;