import React ,{Component}from 'react'
import { Redirect} from 'react-router-dom';
import Tag from './Tag'
import ShareModal from './ShareModal';
import {FaTrash} from 'react-icons/fa'
import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

class FavoriteCard extends Component
{   
    state = {
        redirect: false,
        idPath: ''
    }
    setRedirect =() => {
        this.setState({
            redirect : true,
            
        })
        
    }
    renderRedirect=(id) => {
        

        if (this.state.redirect) {
            console.log(id)
            let path = '/article?id='+ id;
            localStorage.setItem("currentWeb",this.props.newsWeb)
                   
            return <Redirect to={path}/>
        }
    }
    removeLove = (event) => {
        event.stopPropagation();
        var toastContent = "Removing "+this.props.title;
        var favoritesList = JSON.parse(localStorage.getItem("favorites"))
        toast(toastContent, { autoClose: 4000 })
        delete favoritesList[this.props.id];
        localStorage.setItem("favorites", JSON.stringify(favoritesList))
        document.getElementById(this.props.id).style.display = 'none'
        if ( Object.keys(favoritesList).length === 0) {
            document.getElementById("noLove").style.display = 'block'
            document.getElementById("love").style.display = 'none'
        }
        
    }
   
    render() 
    {
        const {title, img, date, section, newsWeb,id} = this.props;
        console.log(section)
        let dateObj = new Date(date);
        let convertDate = dateObj.toISOString().substring(0,10); 
        
        
        return(
            <>
            {this.renderRedirect(id)}
            <p id = "noLove" className = "col-12 col-md 12" style = {{display : "none", textAlign: "center", marginTop:"-20px"}}>
                You have no saved articles
            </p>
            <ToastContainer style = {{"text-align": "left","color":"black"}} position = "top-center" hideProgressBar/>
            <div id = {id} className ="col-md-3"  style = {{margin:0}} onClick = {this.setRedirect}>
                <div className = "resultCard">

                <h1 className = "resultCard-title">{title}
                <ShareModal newsWeb = {newsWeb} title = {title}></ShareModal>
                <button style = {{"background":"none", "border" :"none", margin: "0", padding:"0"}} onClick = {this.removeLove}>
                    <FaTrash tyle = {{"fontSize": "19px"}}/>
                </button>
                </h1>
                
                <div className="imgContainer">
                <img className ="resultCard-img" src={img} alt="ss"></img>
                </div>
                <div className="row" style = {{"margin-top": "15px"}}>
                    <p className = "col-md-6 col-6 resultCard-date">{convertDate}</p>
                    <div className = "col-md-6 col-6">
                    <Tag className = "col-md-6 col-6"size ={"10px"}  section = {section} ></Tag>  
                    <Tag className = "col-md-6 col-6"size ={"10px"}  section = {newsWeb} ></Tag> 
                    </div>
                    
                </div>
                </div>
                
            </div> 
        
        
            </>
           
        )

    }
    
}  

export default FavoriteCard;